.obj-filter {
  background-color: $second-color;
  padding: 15px;
  color: $text-color-white;

  @media (min-width: $screen-lg) {
    padding: 25px 40px;
  }

  &__title {
    font-size: 22px;
  }

  &__wrap {
    padding-top: 15px;

    @media (min-width: $screen-lg) {
      display: flex;
      flex-wrap: wrap;
    }
  }

  &__complex,
  &__inner,
  &__rooms-sel,
  &__results {
    padding: 10px 0;
  }

  &__complex-title,
  &__deadline-title,
  &__rooms-sel-title {
    margin-left: 15px;
    margin-bottom: 10px;

    @media (min-width: $screen-lg) {
      margin-left: 0;
    }
  }

  &__complex-wrap .select2-selection,
  &__deadline-wrap .select2-selection,
  &__rooms-sel-wrap .select2-selection {
    height: 45px;
    padding: 7px 0;
    background-color: $second-color;
    border: 1px solid #fff;
    border-radius: 0;
    outline: none;
  }

  &__complex-wrap  .select2-container:after,
  &__deadline-wrap .select2-container:after {
    content: '';
    display: block;
    position: absolute;
    width: 15px;
    height: 15px;
    right: 20px;
    top: 11px;
    border-right: 1px solid $text-color-white;
    border-bottom: 1px solid $text-color-white;
    transform: rotate(45deg);
    transition: all .5s;
  }

  &__complex-wrap  .select2-container--open:after,
  &__deadline-wrap .select2-container--open:after {
    transform: rotate(225deg);
    top: 17px;
  }

  //&__complex-wrap .select2-selection__arrow,
  //&__deadline-wrap .select2-selection__arrow,
  //&__rooms-sel-wrap .select2-selection__arrow {
  //  top: 9px !important;
  //  right: 10px !important;
  //}

  .select2-container--default .select2-search--inline .select2-search__field {
    cursor: pointer;
  }

  //&__deadline-wrap .select2-selection__placeholder,
  //&__complex-wrap .select2-selection__rendered,
  //&__deadline-wrap .select2-selection__rendered,
  //&__rooms-sel-wrap .select2-selection__placeholder,
  //&__rooms-sel-wrap .select2-selection__rendered {
  //  color: $text-color-white !important;
  //}

  &__complex-wrap input[type='search']::placeholder,
  &__deadline-wrap input[type='search']::placeholder,
  &__rooms-sel-wrap input[type='search']::placeholder {
    color: $text-color-white !important;
  }

  &__complex-wrap
    .select2-container--default.select2-container--focus
    .select2-selection--multiple,
  &__deadline-wrap
    .select2-container--default.select2-container--focus
    .select2-selection--multiple,
  &__rooms-sel-wrap
    .select2-container--default.select2-container--focus
    .select2-selection--multiple {
    border: 1px solid $text-color-white;
    cursor: pointer;
  }

  &__complex-wrap .select2-selection__arrow b,
  &__deadline-wrap .select2-selection__arrow b,
  &__rooms-sel-wrap .select2-selection__arrow b {
    border-color: $text-color-white transparent transparent transparent !important;
  }

  &__complex-wrap .select2-container--open .select2-selection__arrow b,
  &__deadline-wrap .select2-container--open .select2-selection__arrow b,
  &__rooms-sel-wrap .select2-container--open .select2-selection__arrow b {
    border-color: transparent transparent $text-color-white transparent !important;
  }

  &__complex,
  &__inner {
    @media (min-width: $screen-lg) {
      display: inline-block;
    }
  }

  &__complex {
    @media (min-width: $screen-lg) {
      width: 25%;
    }
  }

  &__inner {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    margin-left: -10px;
    margin-right: -10px;

    @media (min-width: $screen-lg) {
      width: 75%;
      margin: 0;
    }
  }

  &__deadline,
  &__build-apartments {
    width: 50%;
    padding: 0 10px;
  }

  &__deadline {
    @media (min-width: $screen-lg) {
      width: 35%;
    }
  }

  &__deadline-wrap {
    @media (min-width: $screen-sm) {
      width: 270px;
    }
  }

  &__build-apartments {
    @media (min-width: $screen-lg) {
      width: 30%;
      padding-right: 0;
      padding-left: 30px;
    }
  }

  &__rooms {
    display: none;

    @media (min-width: $screen-lg) {
      display: inline-block;
      width: 35%;
    }
  }

  &__rooms-sel {
    @media (min-width: $screen-lg) {
      display: none;
    }
  }

  &__rooms-wrap {
    display: flex;
    margin-left: -10px;
    margin-right: -10px;
  }

  &__rooms-count {
    width: 25%;
    height: 45px;
    padding: 0 10px;
    cursor: pointer;
  }

  &__rooms-count input[type='checkbox'] {
    display: none;
  }

  &__rooms-count label {
    display: block;
    font-size: 28px;
    height: 100%;
    text-align: center;
    line-height: 45px;
    border: 1px solid $text-color-white;
  }

  input[type='checkbox']:checked + label {
    background-color: $text-color-white;
    color: $second-color;
  }

  &__build-apartments-btn {
    width: 100%;
    height: 45px;
    font-size: 17px;
    border: 1px solid $second-color;
    background-color: $text-color-white;
    color: $second-color;
    outline: none;
    cursor: pointer;
  }

  &__results {
    display: flex;
    margin-top: 10px;

    @media (min-width: $screen-lg) {
      display: flex;
      flex-direction: row-reverse;
      width: 100%;
    }
  }

  &__results-text {
    font-size: 17px;
    padding: 0 20px;
    order: 1;
    width: 50%;
    margin-bottom: 0;
    text-align: center;

    @media (min-width: $screen-lg) {
      width: 30%;
      font-size: 22px;
    }
  }

  &__results-btn {
    width: 50%;
    min-height: 45px;
    font-size: 16px;
    border: none;
    outline: none;
    cursor: pointer;

    @media (min-width: $screen-lg) {
      width: 20%;
    }
  }
}

.select2-results__option {
  background: $second-color;
  color: $text-color-white;
}
